import 'rc-tree-select/assets/index.less';
import React from 'react';
import ReactDOM from 'react-dom';
import 'rc-dialog/assets/index.css';
import TreeSelect, { TreeNode, SHOW_PARENT } from 'rc-tree-select';
import './demo.less';
import {SHOW_ALL} from "../src";

const dataSource = {
  "result": 1,
  "categoryList": [
    {
      "categoryId": 0,
      "parentCategoryId": -1,
      "categoryName": "未知",
      "isLeaf": 0,
      "path": "0",
      "children": null
    },
    {
      "categoryId": 1,
      "parentCategoryId": -1,
      "categoryName": "美妆",
      "isLeaf": 0,
      "path": "1",
      "children": [
        {
          "categoryId": 103,
          "parentCategoryId": 1,
          "categoryName": "美妆产品购买攻略",
          "isLeaf": 0,
          "path": "1,103",
          "children": [
            {
              "categoryId": 10301,
              "parentCategoryId": 103,
              "categoryName": "种草攻略",
              "isLeaf": 1,
              "path": "1,103,10301",
              "children": null
            },
            {
              "categoryId": 10302,
              "parentCategoryId": 103,
              "categoryName": "购买地点",
              "isLeaf": 1,
              "path": "1,103,10302",
              "children": null
            },
            {
              "categoryId": 10303,
              "parentCategoryId": 103,
              "categoryName": "其他",
              "isLeaf": 1,
              "path": "1,103,10303",
              "children": null
            }]
        },
        {
          "categoryId": 104,
          "parentCategoryId": 1,
          "categoryName": "男士护肤",
          "isLeaf": 1,
          "path": "1,104",
          "children": null
        },
        {
          "categoryId": 105,
          "parentCategoryId": 1,
          "categoryName": "秀晒炫",
          "isLeaf": 1,
          "path": "1,105",
          "children": null
        },
        {
          "categoryId": 106,
          "parentCategoryId": 1,
          "categoryName": "其他",
          "isLeaf": 1,
          "path": "1,106",
          "children": null
        },
        {
          "categoryId": 107,
          "parentCategoryId": 1,
          "categoryName": "保健品",
          "isLeaf": 1,
          "path": "1,107",
          "children": null
        }]
    },
  ]
};
class Demo extends React.Component {
  state = {
    value: undefined
  };

  onChange = value => {
    console.log(value);
    this.setState({ value });
  };

  render() {
    const loop = data =>
      data.map(item => {
        let { categoryId, categoryName, children, isLeaf, path } = item;

        if (children && children.length) {
          return (
            <TreeNode
              key={categoryId}
              value={`${categoryId}-${path}`}
              title={categoryName}
              // isLeaf={Boolean(isLeaf)}
              disabled={!Boolean(isLeaf)}
              disableCheckbox={true}
            >
              {loop(children)}
            </TreeNode>
          );
        }
        return (
          <TreeNode
            key={categoryId}
            value={`${categoryId}-${path}`}
            title={categoryName}
            // isLeaf={Boolean(isLeaf)}
            disabled={!Boolean(isLeaf)}
            disableCheckbox={true}
          />
        )
      });
    return (
      <div style={{ paddingTop: 600 }}>
        <TreeSelect
          showSearch
          style={{ width: 300 }}
          dropdownStyle={{ maxHeight: 400, overflow: "auto" }}
          treeDefaultExpandAll
          placeholder="Please select"
          allowClear
          multiple
          treeNodeFilterProp="title"
        >
          {loop(dataSource.categoryList)}
        </TreeSelect>
      </div>
    );
  }
}

ReactDOM.render(<Demo />, document.getElementById('__react-content'));



